<template>
  <div id="mobileUserAdd">
    <Modal
      v-model="mobileUserBuild"
      title="新建消息提醒"
      @on-cancel="reset"
    >
      <Form ref="add" :model="AddData" :rules="AddRule" :label-width="90" style="padding:0 50px;">
        <FormItem prop="cbd002" label="用户名称：">
          <Input v-model.trim="AddData.cbd002" placeholder="请输入用户名称" size="default"/>
        </FormItem>
        <FormItem prop="cbd005" label="消息标题：">
          <Input v-model.trim="AddData.cbd005" placeholder="请输入消息标题" size="default"/>
        </FormItem>
        <FormItem prop="cbd003" label="发布时间：" v-show="msgsettime">
          <DatePicker type="date" v-model.trim="AddData.cbd003" placement="bottom-end" placeholder="请选择时间" style="width: 100%" size="small"></DatePicker>
        </FormItem>
        <FormItem prop="cbd006" label="消息内容：">
          <Input v-model.trim="AddData.cbd006" placeholder="请输入消息内容" size="default"/>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="addSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { st4ScsCbdAdd } from '@/api/systemManage'
export default {
  name: 'strategyAdd',
  data () {
    return {
      /* 添加数据 */
      AddData: {},
      AddRule: {
        cbd002: [
          { required: true, message: '用户名称不能为空', trigger: 'blur' }
        ],
        cbd003: [
          { required: true, message: '时间不能为空', trigger: 'blur', type: 'date' }
        ],
        cbd005: [
          { required: true, message: '消息标题不能为空', trigger: 'blur' }
        ],
        cbd006: [
          { required: true, message: '消息内容不能为空', trigger: 'blur' }
        ]
      },
      /* 新建框 */
      mobileUserBuild: false
    }
  },
  watch: {
    msgsettime (val) {
      if (val === 0) {
        this.AddData.cbd003 = new Date()
      }
    }
  },
  props: {
    /* 行政区划数据 */
    codeData: {},
    msgsettime: {}
  },
  methods: {
    /* axios: 添加数据 */
    addSubmit () {
      this.$refs.add.validate(valid => {
        if (valid) {
          st4ScsCbdAdd({
            data: {
              ...this.AddData,
              id: this.AddData.cbd001,
              time: this.AddData.cbd003,
              user: this.AddData.cbd002,
              midContent: this.AddData.cbd005,
              afterContent: this.AddData.cbd006
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$emit('getTableData')
              this.$Notice.success({ title: '新建消息提醒成功' })
              this.mobileUserBuild = false
              this.AddData = {}
              this.reset()
            } else {
              this.$Notice.error({ title: res.data.msg })
            }
          })
        }
      })
    },
    // 重置表單參數
    reset () {
      this.AddData = {}
      this.$refs.add.resetFields()
    }
  }
}
</script>

<style scoped lang="scss">
</style>
