<template>
  <div id="mobileUserManagement">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" style="width: calc(100% - 165px)" ref="getQuery" id="msgQuery"></QueryBox>
      <div class="queryBtns">
         <Button type="primary" size="small" @click="releaseModal = true" style="margin-right: 10px">发布规则设置</Button>
        <Button type="success" size="small" icon="md-add" class="btn-add" @click.stop="$refs.strategyAdd.mobileUserBuild = true">新建</Button>
      </div>
    </div>
    <div class="content_box">
      <div>
        <Table border stripe :loading="loading" :columns="columns2" :data="data1" highlight-row size="small" :max-height="tableHeight"></Table>
        <div class="page_box" id="pageBox">
          <Page :total="total" ref="pages" :page-size="pageSize" :current.sync="pageNum" size="small" @on-change="pageChange" show-total show-elevator/>
        </div>
      </div>
    </div>
    <!-- 新建-->
    <messageAdd ref="strategyAdd" :codeData="codeData" @getTableData="getClearQueryData" :msgsettime="msgsettime"></messageAdd>
    <!-- 修改-->
    <messageEdit ref="strategyEdit" :codeData="codeData" :editDefaultData="editDefaultData" :msgsettime="msgsettime"
                  @getTableData="getTableList"></messageEdit>
    <Modal v-model="releaseModal" title="发布规则设置">
      <Form ref="edit" :label-width="90" style="padding:0 50px;">
        <FormItem prop="cbd002" label="发布时间：">
          <RadioGroup v-model="msgsettime">
            <Radio :label="0">
              <span>马上发布</span>
            </Radio>
            <Radio :label="1">
              <span>指定时间发布</span>
            </Radio>
          </RadioGroup>
        </FormItem>
        <FormItem prop="cbd005" label="触发条件：">
          <RadioGroup v-model="msgsetverify">
            <Radio :label="0">
              <span>无需审核</span>
            </Radio>
            <Radio :label="1">
              <span>需审核</span>
            </Radio>
          </RadioGroup>
        </FormItem>
        <FormItem prop="cbd006" label="发布对象：">
          <CheckboxGroup v-model="msgsetuser">
            <Checkbox :label="item.id.toString()" v-for="(item, index) in roleList" :key="index">
              <span>{{item.name}}</span>
            </Checkbox>
          </CheckboxGroup>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="msgsetUpdate">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import QueryBox from '@/components/publicQuery'
import messageAdd from './messageAdd'
import messageEdit from './messageEdit'
import { st4ScsCbdList, st4ScsCbdUpdate, st4ScsCbdDelete, list, st4ScsCbdVerify, msgsetList, msgsetUpdate } from '@/api/systemManage'
import { renderTime } from '@/utils/common'
export default {
  name: 'logManagement',
  data () {
    return {
      /* 表格高度 */
      tableHeight: window.innerHeight - 290,
      /* 查询框查询条件 */
      queryData: {},
      /* 表格数据 */
      columns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '用户名称',
          align: 'center',
          key: 'cbd002'
        },
        {
          title: '消息标题',
          align: 'center',
          key: 'cbd005'
        },
        {
          title: '消息内容',
          align: 'center',
          key: 'cbd006'
        },
        {
          title: '发布时间',
          align: 'center',
          width: 150,
          render: (h, params) => {
            return h('span', renderTime(params.row.cbd003))
          }
        },
        {
          title: '审核',
          align: 'center',
          render: (h, params) => {
            let tf = ''
            switch (params.row.verify) {
              case 0:
                tf = '未审核'
                break
              case 1:
                tf = '审核通过'
                break
              default:
                tf = '未审核'
                break
            }
            return h('span', tf)
          }
        },
        {
          title: '操作',
          key: '',
          width: 240,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              // 发布
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small',
                  icon: 'md-checkbox-outline'
                },
                style: {
                  marginRight: '5px',
                  display: this.msgsetverify === 0 ? 'none' : 'inline-block'
                },
                on: {
                  click: () => {
                    this.st4ScsCbdVerify(params.row.cbd001)
                  }
                }
              }, '审核'),
              // 修改
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.editDefaultData = params.row
                    this.$refs.strategyEdit.mobileUserRebuild = true
                  }
                }
              }, '编辑'),
              // 删除
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small',
                  icon: 'md-trash'
                },
                on: {
                  click: () => {
                    this.$Modal.confirm({
                      title: '确认',
                      content: '请问您确定要删除该策略吗？',
                      onOk: () => {
                        this.infoDel(params.row.cbd001)
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, '删除')
            ])
          }
        }
      ],
      data1: [],
      /* 总条数 */
      total: 0,
      pageNum: 1,
      pageSize: Math.floor((window.innerHeight - 290) / 40) - 1,
      /* 加载 */
      loading: false,
      /* 编辑框默认原数据 */
      editDefaultData: {},
      /* 行政区划数据 */
      codeData: [],
      roleList: [],
      /* 发布 */
      releaseModal: false,
      EditData: {},
      /* 规则 */
      msgsettime: 1,
      msgsetverify: 1,
      msgsetuser: []
    }
  },
  components: {
    QueryBox,
    messageAdd,
    messageEdit
  },
  computed: {
    // 查询表单参数
    queryFrom: function () {
      return [
        {
          type: 1,
          field: 'title',
          title: '消息标题'
        },
        {
          type: 1,
          field: 'content',
          title: '消息内容'
        },
        {
          type: 5,
          field: 'time',
          title: '发布时间'
        }
      ]
    },
    // 表字段过滤
    columns2: function () {
      let arr = this.columns1
      if (this.msgsettime === 0) {
        arr = arr.filter((res) => {
          return res.title !== '发布时间'
        })
      }
      if (this.msgsetverify === 0) {
        arr = arr.filter((res) => {
          return res.title !== '审核'
        })
      }
      return arr
    }
  },
  created () {
    this.getTableList()
    this.getTableData()
    this.msgsetList()
  },
  mounted () {
    // 窗口改变时table自适应
    window.addEventListener('resize', () => {
      this.tableHeight = window.innerHeight - 290
      this.pageSize = Math.floor((window.innerHeight - 290) / 40) - 1
      this.getTableList()
    }, false)
  },
  methods: {
    /* 获取角色列表数据 */
    getTableData () {
      list({
        data: {
          name: this.name
        }
      }).then((res) => {
        this.roleList = res.data.data.rows
      }).catch((error) => {
        console.log(error)
      })
    },
    /* 获取发布规则设置 */
    msgsetList () {
      msgsetList({
        data: {}
      }).then((res) => {
        this.msgsettime = res.data.data.rows[0].time
        this.msgsetverify = res.data.data.rows[0].verify
        this.msgsetuser = res.data.data.rows[0].user.split(',')
      }).catch((error) => {
        console.log(error)
      })
    },
    /* 修改发布规则 */
    msgsetUpdate () {
      msgsetUpdate({
        data: {
          id: 1,
          time: this.msgsettime * 1,
          user: this.msgsetuser.join(','),
          verify: this.msgsetverify * 1
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.releaseModal = false
          this.msgsetList()
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    /* 获取查询数据 */
    getQueryData (data) {
      this.queryData = data
      this.pageNum = 1
      this.getTableList()
    },
    /* 获取字段列表 */
    getTableList () {
      this.loading = true
      st4ScsCbdList({
        data: {
          pageSize: this.pageSize,
          pageNumber: this.pageNum,
          ...this.queryData
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.rows
          this.total = res.data.data.total
          this.loading = false
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 审核 */
    st4ScsCbdVerify (id) {
      this.loading = true
      st4ScsCbdVerify({
        data: {
          id: id
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.getTableList()
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    },
    /* axios: 删除数据 */
    infoDel (e) {
      st4ScsCbdDelete({
        data: {
          id: e
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '删除成功' })
          // 避免刪除当前页最后一条数据时显示--无数据
          if (this.data1.length === 1 && this.pageNum > 1) {
            this.pageNum = this.pageNum - 1
          }
          this.getTableList()
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 新添加任务之后返回的任务列表同时清空查询条件 */
    getClearQueryData () {
      this.queryData = {}
      this.pageNum = 1
      this.getTableList()
      this.$refs.getQuery.cleardata()
    },
    /* 省市县菜单value = comCode */
    valComCode (arr) {
      for (let i in arr) {
        arr[i].value = arr[i].comCode
        this.valComCode(arr[i].children)
      }
      return arr
    }
  }
}
</script>

<style scoped lang="scss">
  #mobileUserManagement {
    position: relative;
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
  }
</style>
