<template>
  <div id="mobileUserEdit">
    <Modal
      v-model="mobileUserRebuild"
      title="编辑消息提醒">
      <Form ref="edit" :model="EditData" :rules="EditRule" :label-width="90" style="padding:0 50px;">
        <FormItem prop="cbd002" label="用户名称：">
          <Input v-model.trim="EditData.cbd002" placeholder="请输入用户名称" size="default"/>
        </FormItem>
        <FormItem prop="cbd005" label="消息标题：">
          <Input v-model.trim="EditData.cbd005" placeholder="请输入消息标题" size="default"/>
        </FormItem>
        <FormItem prop="cbd003" label="发布时间：" v-show="msgsettime">
          <DatePicker type="date" v-model.trim="EditData.cbd003" placement="bottom-end" placeholder="请选择时间" style="width: 100%" size="default"></DatePicker>
        </FormItem>
        <FormItem prop="cbd006" label="消息内容：">
          <Input v-model.trim="EditData.cbd006" placeholder="请输入消息内容" size="default"/>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="editSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { st4ScsCbdUpdate } from '@/api/systemManage'
export default {
  name: 'strategyEdit',
  data () {
    return {
      EditRule: {
        cbd002: [
          { required: true, message: '用户名称不能为空', trigger: 'blur' }
        ],
        cbd003: [
          { required: true, message: '时间不能为空', trigger: 'blur', type: 'date' }
        ],
        cbd005: [
          { required: true, message: '消息标题不能为空', trigger: 'blur' }
        ],
        cbd006: [
          { required: true, message: '操作信息不能为空', trigger: 'blur' }
        ]
      },
      /* 编辑框 */
      mobileUserRebuild: false,
      /* 编辑数据 */
      EditData: {}
    }
  },
  props: {
    /* 行政区划数据 */
    codeData: {},
    /* 编辑行原数据 */
    editDefaultData: {},
    msgsettime: {}
  },
  watch: {
    editDefaultData (val) {
      this.EditData.cbd001 = val.cbd001
      this.EditData.cbd002 = val.cbd002
      this.EditData.cbd005 = val.cbd005
      this.EditData.cbd003 = new Date(val.cbd003)
      this.$set(this.EditData, 'cbd006', val.cbd006)
    },
    msgsettime (val) {
      if (val === 0) {
        this.EditData.cbd003 = new Date()
      }
    }
  },
  mounted () {
  },
  methods: {
    /* 编辑 */
    editSubmit () {
      console.log(this.EditData)
      this.$refs.edit.validate(valid => {
        if (valid) {
          st4ScsCbdUpdate({
            data: {
              ...this.EditData,
              id: this.EditData.cbd001,
              time: this.EditData.cbd003,
              user: this.EditData.cbd002,
              midContent: this.EditData.cbd005,
              afterContent: this.EditData.cbd006
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '编辑消息成功' })
              this.$emit('getTableData')
              this.mobileUserRebuild = false
              this.data = {}
              this.reset()
            } else {
              this.$Notice.error({ title: res.data.msg })
            }
          })
        }
      })
    },
    // 重置表單參數
    reset () {
      this.EditData = {}
    }
  }
}
</script>

<style scoped lang="scss">
</style>
